.bookings-control{
    text-align: center;
    padding: 0.5rem;
}

.bookings-control button{
    font: inherit;
    border: none;
    background: transparent;
    color: black;
    padding: 0.25rem 1rem;
    border-bottom: transparent;
    cursor: pointer;
}

.bookings-control button.active{
    border-bottom-color: #5101d1;
    color: #5101d1;
}

.bookings-control button:focus{
    outline: none;
}